import React from 'react'
import './Info.css'

const Info =({onRouteChange})=> {
	
		return(
			<>
			  <div className='containerInfo'>
			   <li className="box_Info" onClick={() => onRouteChange('about')}><p className ="aInfo">About</p></li>
			   <li className="box_Info" onClick={() => onRouteChange('contact')}><p className ="aInfo" >Contact</p></li>
			   <li className="box_Info" onClick={() => onRouteChange('shipping')}><p className ="aInfo">Shipping</p></li>
			   <li className="box_Info" onClick={() => onRouteChange('return')}><p className ="aInfo">Return</p></li>
			  
			  </div>
			</>

			)
	}

export default Info