import React  from 'react'
import './Nav.css'
import bag from './bag.png'
import {BrowserRouter as Router} from "react-router-dom";






const Nav = ({navClick, homeClick, onRouteChange}) => {


 
  


    return(
   
      <Router>
      <div className = 'containerNav' >
        <ul className="navul">
           
               <li className="first"  onClick={() => onRouteChange('newin')}><p className="aNav " >New-in</p></li>
               <li className=" second" onClick={() => onRouteChange('woman')}><p className="aNav" >Woman</p></li>
               <li className=" third" onClick={() => onRouteChange('img')} ><p className="boutique">Boutique</p></li>
               <li className="forth" onClick={() => onRouteChange('man')}><p className="aNav" >Man</p></li>
               <li className="fifth" onClick={() => onRouteChange('search')}><p className="aNav" >Search</p></li>
               
        </ul>
              <li className="sixth"  >
                 <div className="divBag"> <p onClick={() => onRouteChange('bag')}>
                      <span className="fa-stack fa-2x has-badge" data-count="0">
                      <img  className ="bag" src={bag}  alt="bag"/>
                      </span>
                 </p></div>
               </li>
           
      </div>



    </Router>

      )


  }






export default Nav