import React, {useState} from 'react'


import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


import g from './g.jpg'
import h from './h.jpg'
import w4 from './w4.jpg'
import d from './d.jpg'

import W4 from './W4'
import D from './D'
import H from './H'
import G from './G'


const Woman = () =>{
  

   const [visible, setVisible] = useState(true);
  
    const onImgClick = () => setVisible(!visible);

    const onBackClick = () => setVisible(true);
    
	return(

  <Router>
  {visible ?
    <>
    
		<div className="containerImg">
    <div className ="boxImg"> 
      <Link to="/w4" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={w4}  alt="w4"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/h" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={h}  alt="h"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg">
      <Link to="/d" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={d}  alt="d"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/g" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={g}  alt="g"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/w4" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={w4}  alt="w4"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/h" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={h}  alt="h"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/d" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={d}  alt="d"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div>
    <div className ="boxImg"> 
      <Link to="/g" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={g}  alt="g"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
   </div>
   </>
  : null}
  <Switch>
          <Route path="/w4">
            <W4 onClick={onBackClick}/>
          </Route>
          <Route path="/h">
            <H />
          </Route>
          <Route path="/d">
            <D />
          </Route>
          <Route path="/g">
            <G/>
          </Route>
        
        
        </Switch>
    </Router>
		)

	}

	export default Woman