import React from 'react'
import './Menu.css'
import {
    Menu,
    MenuItem,
    MenuButton,
    SubMenu
} from '@szhsin/react-menu';
import '@szhsin/react-menu/dist/index.css';


  



 

export default function Menus({onRouteChange}) {
    return (
     
        <Menu menuButton={<MenuButton>
           <div  className="navbar">
             <div className="one"></div>
             <div className="two"></div>
             <div className="three"></div>
          </div>

            </MenuButton>}>
           
            <SubMenu label="Woman">
                <MenuItem onClick={() => onRouteChange('wdress')}><p className ="aMenu">Dress</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('wtshirt')}><p className ="aMenu">T-shirt</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('wtrouser')}><p className ="aMenu">Trouser</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('wcoat')}><p className ="aMenu">Coat</p></MenuItem>
                
            </SubMenu>
            <SubMenu label="Man">
                <MenuItem onClick={() => onRouteChange('mjacket')}><p className ="aMenu">Coat</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('mshirt')}><p className ="aMenu">Shirt</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('mtrouser')}><p className ="aMenu">Trouser</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('mtshirt')}><p className ="aMenu">T-shirt</p></MenuItem>
            </SubMenu>
            <SubMenu label="Sale">
                <MenuItem onClick={() => onRouteChange('sdress')}><p className ="aMenu">Dress</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('sjersey')}><p className ="aMenu">Jersey</p></MenuItem>
                <MenuItem onClick={() => onRouteChange('sshoes')}><p className ="aMenu">Shoes</p></MenuItem>
                
            </SubMenu>
           
           
        </Menu>
  

    
    );
}

