import React, {useState} from 'react'
import '../Detail.css'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Man from './Man';

import h5 from './h5.jpg'
import h5copy from './h5copy.jpg'
import h5copy2 from './h5copy2.jpg'


const H5 = ({onBackClick}) => {

	const [hide, setHide] = useState(true);
  
    const backButton = () => setHide(false);
    
	return(
		<Router>
		  {hide ?
			<>
			 <div className='containerDetail'>
			 <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/man" onClick={onBackClick} >Back</Link></button>

			  <div className='boxDetail'>
			    <img className ="bigimg" src={h5}  alt="h5"  width="400" height="400" />
			    <img className ="bigimg centerimg" src={h5copy2}  alt="h5copy2"  width="400" height="200" />
			    <img className ="bigimg" src={h5copy}  alt="h5copy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>Dark Blue</p><div className="darkblue"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			:null}
		 <Switch>

          <Route path="/man">
            <Man />
          </Route>
         </Switch>
	</Router>
			)

			
	}




export default H5