import React, {useState} from 'react'
import '../Detail.css'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Newin from './Newin'

import new5 from './new5.jpg'
import new5copy from './new5copy.jpg'
import new5copy2 from './new5copy2.jpg'


const Newin5 = ({onBackClick}) => {

	 const [hide, setHide] = useState(true);
  
     const backButton = () => setHide(false);

	return(
	  <Router>
			<>
			{hide ?
			 <div className='containerDetail'>
			 <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/newin" onClick={onBackClick} >Back</Link></button>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={new5}  alt="new5"  width="400" height="400" />
			    <img className ="bigimg middleImg centerimg" src={new5copy2}  alt="new5copy2"  width="400" height="200" />
			    <img className ="bigimg" src={new5copy}  alt="new5copy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>Khaki</p><div className="khaki"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			:null }
			</>
		<Switch>
		  <Route path="/newin">
            <Newin />
          </Route>
        </Switch>
	</Router>
			)

			
	}




export default Newin5