import React, {useState} from 'react'
import '../Detail.css'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Man from './Man'

import h1 from './h1.jpg'
import h1copy from './h1copy.jpg'
import h1copy2 from './h1copy2.jpg'


const H1 = ({onBackClick}) => {

	const [hide, setHide] = useState(true);
  
    const backButton = () => setHide(false);

	return(
		<Router>
		  {hide ?
			<>
			 <div className='containerDetail'>
			 <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/man" onClick={onBackClick} >Back</Link></button>

			  <div className='boxDetail'>
			    <img className ="bigimg" src={h1}  alt="h1"  width="400" height="400" />
			    <img className ="bigimg centerimg" src={h1copy2}  alt="h1copy2"  width="400" height="200" />
			    <img className ="bigimg" src={h1copy}  alt="h1copy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			   <p>Grey</p><div className="grey"></div>
			    <p>Beige</p><div className="beige"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			 :null}
		 <Switch>

          <Route path="/man">
            <Man />
          </Route>
         </Switch>
	</Router>
			)

			
	}




export default H1