import React, {useState} from 'react'
import './Img.css'
import h from './h.jpg'
import b from './b.jpg'
import c from './c.jpg'
import d from './d.jpg'

import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";



import H from './H'
import D from './D'
import C from './C'
import B from './B'




   const Img = () => {

    const [visible, setVisible] = useState(true);
  
    const onImgClick = () => setVisible(!visible);

    const onBackClick = () => setVisible(true);
    
    const Home = () => (
   <div>
   </div>
  );

          return( 
        


    <Router>
     {visible ?
       <div className ="Img" >
       
        <div className ="containerImg">
        <div className ="boxImg">
          <Link to="/h" className ="linkImg" onClick={onImgClick}>
           <img  className ="img" src={h}  alt="h"  width="300" height="300" />
           <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
          </Link>
         </div>
         <div className ="boxImg" >
          <Link to="/d" className ="linkImg" onClick={onImgClick}>
           <img  className ="img" src={d} alt="d"   width="300" height="300" />
           <span className ="tooltiptext">Discription bla bla<p> 35$</p></span>
          </Link>
         </div>
         <div className ="boxImg">
          <Link to="/c" className ="linkImg" onClick={onImgClick} >
           <img className ="img" src={c}  alt="c"   width="300" height="300" />
           <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
          </Link>
         </div>
         <div className ="boxImg" >
          <Link to="/b" className ="linkImg" onClick={onImgClick}>
           <img  className ="img" src={b} alt="b"  width="300" height="300" />
           <span className ="tooltiptext"> Discription bla bla<p> 35$</p></span>
          </Link>
         </div>
          <div className ="boxImg">
          <Link to="/h" className ="linkImg"  onClick={onImgClick}>
           <img  className ="img" src={h}  alt="h"  width="300" height="300" />
           <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
          </Link>
         </div>
         <div className ="boxImg" >
          <Link to="/d" className ="linkImg" onClick={onImgClick}>
           <img  className ="img" src={d} alt="d"   width="300" height="300" />
           <span className ="tooltiptext">Discription bla bla<p> 35$</p></span>
          </Link>
         </div>
         <div className ="boxImg">
          <Link to="/c" className ="linkImg" onClick={onImgClick}>
           <img  className ="img" src={c}  alt="c"   width="300" height="300" />
           <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
         </Link>
         </div>
         <div className ="boxImg" >
          <Link to="/b" className ="linkImg" onClick={onImgClick}>
           <img  className ="img" src={b} alt="b"  width="300" height="300" />
           <span className ="tooltiptext"> Discription bla bla<p> 35$</p></span>
         </Link>
         </div>
    
        </div>
      </div>
   : null}




      <Switch>

          <Route path="/h">
            <H onClick={onBackClick}/>
          </Route>
          <Route path="/d">
            <D />
          </Route>
          <Route path="/c">
            <C/>
          </Route>
          <Route path="/b">
            <B />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
    </Router>
 
)
        }


export default Img