import React from 'react'
import './Detail.css'

import c from './c.jpg'
import ccopy from './ccopy.jpg'
import ccopy2 from './ccopy2.jpg'


const C = () => {

	return(
		
			<>
			 <div className='containerDetail'>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={c}  alt="c"  width="400" height="400" />
			    <img className ="bigimg" src={ccopy2}  alt="ccopy2"  width="400" height="200" />
			    <img className ="bigimg" src={ccopy}  alt="ccopy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>Khaki</p><div className="khaki"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			)

			
	}




export default C