import React from 'react'
import { Link} from "react-router-dom";
import './Bag.css'

import h from './h.jpg'

const Bag = () =>{

	return(
		<div className='containerBag'>
  <h1>Selected Items</h1>
  <hr/>
  <table className='boxBag'>
   <tr>
     <th>Product</th>
     <th>Discription</th>
     <th>Pcs</th>
     <th>Price</th>
    </tr>
    <tr>
     <td>
         <Link to="/H" className ="linkImg" >
            <img src={h}  alt="h"  width="50" height="50"/>
         </Link>
     </td>
     <td>Beige cotton top, made in India </td>
     <td>1</td>
     <td>35$</td>
    </tr>
  </table>
  <hr/>
    <div className='smallbox'>
     <div className='total'>
      <p>Total:</p>
      <p>35$</p>
     </div>
     </div>
    <button className='buy'>Buy</button>
  

  </div>

		)}

export default Bag