import React, {useEffect} from 'react'



const Shipping =({onRouteChange}) => {
  

  useEffect(() => {
  window.scrollTo(0, 0)
}, [])
  
	return(
  <div >
   <button  className="backBtn" onClick={()=>onRouteChange('img')} ><p  className=" backLink" >Back</p></button>
   <h3>Shipping Policy</h3>
   <hr/>
   <div className= 'aboutContainer'>
       <h4>Shipping</h4>
	   <p>
	   All orders are processed within X to X business days (excluding weekends and holidays) after receiving your order confirmation email. You will receive another notification when your order has shipped. 
	   	</p>
	   <h4>Domestic Shipping Rates and Estimates</h4>
	   <p>For calculated shipping rates: Shipping charges for your order will be calculated and displayed at checkout. 

For simple flat rate shipping: We offer $X flat rate shipping to [list countries]. 

You can also emphasize any free shipping thresholds you offer (e.g. free shipping for orders over $75). For multiple shipping options, you can list carrier options, prices, and delivery times in a table.</p>
	    <table>
		  <tr>
		    <th>Shipping option</th>
		    <th>Estimated delivery time</th>
		    <th>Price</th>
		  </tr>
		  <tr>
		    <td>Option 1</td>
		    <td>X to X business days</td>
		    <td>$X</td>
		  </tr>
		  <tr>
		    <td>Option 2</td>
		    <td>X to X business days</td>
		    <td>$X</td>
		  </tr>
		  <tr>
		    <td>Option 3</td>
		    <td>X to X business days</td>
		    <td>$X</td>
		  </tr>
		</table> 
	   <h4>Local delivery</h4>
	   <p>Free local delivery is available for orders over $X within [area of coverage]. For orders under $X, we charge $X for local delivery.

Deliveries are made from [delivery hours] on [available days]. We will contact you via text message with the phone number you provided at checkout to notify you on the day of our arrival. </p>
	   <h4>In-store pickup</h4>
	   <p>You can skip the shipping fees with free local pickup at [list the locations where in-store pickup is available]. After placing your order and selecting local pickup at checkout, your order will be prepared and ready for pick up within X to X business days. We will send you an email when your order is ready along with instructions. 

Our in-store pickup hours are [store hours] on [available days of the week]. Please have your order confirmation email with you when you come.</p>
	   <h4>International Shipping</h4>
	   <p>We offer international shipping to the following countries: [list of countries]. 

If relevant you can also include countries you don’t ship to: At this time, we do not ship to [list of countries]. 

If you’re using calculated shipping rates: Shipping charges for your order will be calculated and displayed at checkout. </p>
<table className='table2'>
		  <tr>
		    <th>Shipping option</th>
		    <th>Estimated delivery time</th>
		    <th>Price</th>
		  </tr>
		  <tr>
		    <td>Option 1</td>
		    <td>X to X business days</td>
		    <td>$X</td>
		  </tr>
		  <tr>
		    <td>Option 2</td>
		    <td>X to X business days</td>
		    <td>$X</td>
		  </tr>
		  <tr>
		    <td>Option 3</td>
		    <td>X to X business days</td>
		    <td>$X</td>
		  </tr>
		</table> 
	</div>
  </div>
)

}


export default Shipping