import React,  {useState} from 'react'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import h1 from './h1.jpg'
import h2 from './h2.jpg'
import h4 from './h4.jpg'
import h5 from './h5.jpg'

import H1 from './H1'
import H2 from './H2'
import H4 from './H4'
import H5 from './H5'




const Man = () =>{

    const [visible, setVisible] = useState(true);
  
    const onImgClick = () => setVisible(!visible);

    const onBackClick = () => setVisible(true);
    


 
	return(
   <Router>
   
    
    {visible ?
      <>
   
		<div className="containerImg">
    <div className ="boxImg"> 
      <Link to="/h4" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={h4}  alt="h4"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/h1" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={h1}  alt="h1"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg">
      <Link to="/h2" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={h2}  alt="h2"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link  to="/h5" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={h5}  alt="h5"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/h4" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={h4}  alt="h4"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/h1" className="linkImg"onClick={onImgClick}>
       <img className ="img" src={h1}  alt="h1"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg">
      <Link to="/h2" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={h2}  alt="h2"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/h5" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={h5}  alt="h5"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
   </div>
   </>
    : null}
 
    <Switch>
          <Route path="/h4">
            <H4 onClick={onBackClick}/>
          </Route>
          <Route path="/h1">
            <H1 />
          </Route>
          <Route path="/h2">
            <H2 />
          </Route>
          <Route path="/h5">
            <H5 />
          </Route>
          
        </Switch>
    </Router>


		)}



export default Man