import React, {useState} from 'react'
import '../Detail.css'

import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Newin from './Newin'

import new7 from './new7.jpg'
import new7copy from './new7copy.jpg'
import new7copy2 from './new7copy2.jpg'


const Newin7 = ({onBackClick}) => {

	 const [hide, setHide] = useState(true);
  
     const backButton = () => setHide(false);
	return(
		<Router>
			<>
			{hide ?
			 <div className='containerDetail'>
			  <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/newin" onClick={onBackClick} >Back</Link></button>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={new7}  alt="new7"  width="400" height="400" />
			    <img className ="bigimg centerimg" src={new7copy2}  alt="new7copy2"  width="400" height="200" />
			    <img className ="bigimg" src={new7copy}  alt="new7copy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>White</p><div className="white"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			:null }
			</>
		<Switch>
		  <Route path="/newin">
            <Newin />
          </Route>
        </Switch>
	</Router>
			)

			
	}




export default Newin7