import React from 'react'
import './Detail.css'

import d from './d.jpg'
import dcopy from './dcopy.jpg'
import dcopy2 from './dcopy2.jpg'


const D = () => {

	return(
		
			<>
			 <div className='containerDetail'>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={d}  alt="d"  width="400" height="400" />
			    <img className ="bigimg" src={dcopy2}  alt="dcopy2"  width="400" height="200" />
			    <img className ="bigimg" src={dcopy}  alt="dcopy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>White</p><div className="white"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			)

			
	}




export default D