import React, {useState} from 'react'
import '../Detail.css'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Man from './Man'


import h4 from './h4.jpg'
import h4copy from './h4copy.jpg'
import h4copy2 from './h4copy2.jpg'


const H4 = ({onBackClick}) => {

	const [hide, setHide] = useState(true);
  
    const backButton = () => setHide(false);
    

	return(
		 <Router>
		  {hide ?
			<>
			 <div className='containerDetail'>
			 <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/man" onClick={onBackClick} >Back</Link></button>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={h4}  alt="h4"  width="400" height="400" />
			    <img className ="bigimg middleImg centerimg" src={h4copy2}  alt="h4copy2"  width="400" height="200" />
			    <img className ="bigimg" src={h4copy}  alt="h4copy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>Grey</p><div className="grey"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
		  :null}
		 <Switch>

          <Route path="/man">
            <Man />
          </Route>
         </Switch>
	</Router>
			)

			
	}




export default H4