import React from 'react';

import Items from './Items';


const List= ({items}) => {

	const listComponent = items.map( (category, i) => {

		return(
			<Items
			  key={i}
			  id={items[i].id}
			  title={items[i].title}
			  category={items[i].category} 
			  image={items[i].image}
			  price={items[i].price}/>
			)
	})
	return(

		<div>{listComponent}</div>
		);
}


export default List;