import React, {Component} from 'react'
import Menus from '../component/menu/Menus';
import Nav from '../component/nav/Nav';
import Img from '../component/img/Img';
import Social from '../component/footer/Social'
import Info from '../component/footer/Info';

import About from '../component/footer/About';
import Contact from '../component/footer/Contact';
import Shipping from '../component/footer/Shipping';
import Return from '../component/footer/Return';

import Newin from '../component/nav/newin/Newin';
import Woman from '../component/nav/woman/Woman';
import Man from '../component/nav/man/Man';
import Search from '../component/nav/search/Search';
import Bag from '../component/nav/bag/Bag';

 import Wdress from '../component/menu/wcloth/Wdress';
 import Wtshirt from '../component/menu/wcloth/Wtshirt';
 import Wtrouser from '../component/menu/wcloth/Wtrouser';
 import Wcoat from '../component/menu/wcloth/Wcoat';

 import Mjacket from '../component/menu/mcloth/Mjacket';
 import Mshirt from '../component/menu/mcloth/Mshirt';
 import Mtrouser from '../component/menu/mcloth/Mtrouser';
 import Mtshirt from '../component/menu/mcloth/Mtshirt';

 import Sdress from '../component/menu/sales/Sdress';
 import Sjersey from '../component/menu/sales/Sjersey';
 import Sshoes from '../component/menu/sales/Sshoes';

import './App.css';
import Helmet from 'react-helmet';



class App extends Component{

constructor(props) {
    super(props);
    this.state = {
      route:'img',
      visible: true,
     };
}

  navClick = () => {
   this.setState ({ visible: false
   }) 
   
}
  homeClick = () => {
  this.setState ({visible:true})
}
 onRouteChange = (route) =>{
          this.setState({route: route})
        }
componentDidMount() {
  window.scrollTo(0, 0)
}
  
render() {

  const {route} = this.state
    return (
      
      <div className="container">
       <Helmet>
          <title>Online Shop</title>
          <meta name="viewport" content="width=device-width,initial-scale=1,maximum-scale=1,user-scalable=no"/>
          <meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1"/>
          <meta name="HandheldFriendly" content="true"/>
       </Helmet>


        <Menus onRouteChange={this.onRouteChange}
 />
        <Nav  navClick={this.navClick}
               homeClick={this.homeClick}
               onRouteChange={this.onRouteChange}

               
         />
        {route === 'img' ?
         
       ( this.state.visible 
            ?<Img  imgClick/>
            : null
           
        )

      : ( route === 'about' ?
          <About onRouteChange={this.onRouteChange} />
          : (route === 'contact' ?
          <Contact onRouteChange={this.onRouteChange}/>
            : (route === 'shipping' ?
            <Shipping onRouteChange={this.onRouteChange}/>
              : (route === 'return' ? 
              <Return onRouteChange={this.onRouteChange}/>
              :  (route === 'newin' ?
                <Newin />
                : (route === 'woman' ?
                  <Woman />
                  : (route === 'man' ?
                   <Man />
                    : (route === 'search' ?
                     <Search />
                     : (route === 'bag' ?
                      <Bag />
                      : (route === 'wdress' ?
                       <Wdress />
                       : (route === 'wtshirt' ?
                         <Wtshirt />
                         : (route === 'wtrouser' ?
                          <Wtrouser />
                          : (route === 'wcoat' ?
                           <Wcoat />
                           : (route === 'mjacket' ?
                            <Mjacket />
                            : (route === 'mshirt' ?
                             <Mshirt />
                             : (route === 'mtrouser' ?
                              <Mtrouser />
                              :  (route === 'mtshirt' ?
                               <Mtshirt />
                               :  (route === 'sdress' ?
                                <Sdress />
                                : (route === 'sjersey' ?
                                 <Sjersey />
                                 :
                                  <Sshoes />
                             )
                            )
                           )

                          )
                         )
                        )
                       )
                      )
                     )

                    )

                   )
                  )
                )
               )
              )

            )

          )
        )
       ) 
    }
      
        <div className='social'>  
          <Social />
        </div>

        <Info onRouteChange={this.onRouteChange} />
        
      </div>

     
  
    )
    

    
  }
 
}




	
export default App