import React, {useState} from 'react'
import '../Detail.css'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Woman from './Woman'

import w4 from './w4.jpg'
import  w4copy from './w4copy.jpg'
import  w4copy2 from './w4copy2.jpg'


const W4 = ({onBackClick}) => {

	const [hide, setHide] = useState(true);
  
    const backButton = () => setHide(false);

	return(
	 <Router>
	        {hide ?
			<>
			 <div className='containerDetail'>
			 <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/woman" onClick={onBackClick} >Back</Link></button>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={ w4}  alt=" w4"  width="400" height="400" />
			    <img className ="bigimg middleImg centerimg" src={ w4copy2}  alt=" w4copy2"  width="400" height="200" />
			    <img className ="bigimg" src={ w4copy}  alt=" w4copy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>White</p><div className="white"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			:null}
		<Switch>

          <Route path="/woman">
            <Woman />
          </Route>
        </Switch>
	 </Router>
			)

			
	}




export default W4