import React from 'react'
import './Detail.css'

import b from './b.jpg'
import bcopy from './bcopy.jpg'
import bcopy2 from './bcopy2.jpg'


const B = () => {

	return(
		
			<>
			 <div className='containerDetail'>
			  <div className='boxDetail'>
			    <img className ="bigimg" src={b}  alt="b"  width="400" height="400" />
			    <img className ="bigimg" src={bcopy2}  alt="bcopy2"  width="400" height="200" />
			    <img className ="bigimg" src={bcopy}  alt="bcopy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>Bronze</p><div className="bronze"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			)

			
	}




export default B