import React, {useState}  from 'react'
import new2 from './new2.jpg'
import new4 from './new4.jpg'
import new5 from './new5.jpg'
import new7 from './new7.jpg'


import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Newin5 from './Newin5'
import Newin2 from './Newin2'
import Newin4 from './Newin4'
import Newin7 from './Newin7'

const Newin = () =>{

    const [visible, setVisible] = useState(true);
  
    const onImgClick = () => setVisible(!visible);

    const onBackClick = () => setVisible(true);
    

		return(
      <Router>
    {visible ?
			<>
      
			<h1 className="header">New Collection</h1>
   <div className="containerImg">
    <div className ="boxImg"> 
      <Link to="/newin5" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={new5}  alt="new5"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/newin2" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={new2}  alt="new2"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg">
      <Link to="/newin4" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={new4}  alt="new4"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/newin7" className="linkImg" onClick={onImgClick}>   
       <img  className ="img" src={new7}  alt="new7"  width="300" height="300" />
        <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/newin5" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={new5}  alt="new5"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/newin2" className="linkImg" onClick={onImgClick}>
       <img className ="img" src={new2}  alt="new2"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
    <div className ="boxImg"> 
      <Link to="/newin4" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={new4}  alt="new4"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div>
    <div className ="boxImg"> 
      <Link to="/newin7" className="linkImg" onClick={onImgClick}>   
       <img className ="img" src={new7}  alt="new7"  width="300" height="300" />
         <span className ="tooltiptext"> Discription bla bla<p> 35$</p> </span>
      </Link>
    </div> 
   </div>
			</>

 : null}

      <Switch>

          <Route path="/newin5">
            <Newin5 onClick={onBackClick}/>
          </Route>
          <Route path="/newin2">
            <Newin2 />
          </Route>
          <Route path="/newin4">
            <Newin4 />
          </Route>
          <Route path="/newin7">
            <Newin7 />
          </Route>
        
        </Switch>
    </Router>
			)
	}


export default Newin