import React, {useState} from 'react'
import '../Detail.css'
import {BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Woman from './Woman';

import d from './d.jpg'
import dcopy from './dcopy.jpg'
import dcopy2 from './dcopy2.jpg'


const D = ({onBackClick}) => {

	const [hide, setHide] = useState(true);
  
    const backButton = () => setHide(false);

	return(
		<Router>
	        {hide ?
			<>
			 <div className='containerDetail'>
			 <button className="backButton"  onClick={backButton}><Link  className="backLink" to="/woman" onClick={onBackClick} >Back</Link></button>

			  <div className='boxDetail'>
			    <img className ="bigimg" src={d}  alt="d"  width="400" height="400" />
			    <img className ="bigimg centerimg" src={dcopy2}  alt="dcopy2"  width="400" height="200" />
			    <img className ="bigimg" src={dcopy}  alt="dcopy"  width="400" height="400" />
			  </div>   <hr/>

              <div className="extras">
			   <div className="color">
			    
			    <p>Black</p><div></div>
			    <p>White</p><div className="white"></div>

			  </div>
			 </div>



			 <div className="size">
			   
			    <div><p>XS</p></div>
			    <div><p>S</p></div>
			    <div><p>M</p></div>
			    <div><p>L</p></div>
			    <div><p>XL</p></div>
			  </div>
			 <div className="add">
			    <button>Add+</button>
			  </div>
			    <hr/>
			</div>
			</>
			:null}
		<Switch>

          <Route path="/woman">
            <Woman />
          </Route>
        </Switch>
	 </Router>
			)

			
	}




export default D